<template>
	<div id="nav">
		<div class="nav">
			<div class="nav_list">
				<ul id="nav_ul">
					<li><span class="user">admin</span></li>
					<li><span></span></li>
					<li><span class="personal"></span></li>
					<li><span class="password"></span></li>
					<li><span class="quit"></span></li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
export default {	
	data () {
    return {user:{
							name:"admin",
							role:"",
							msgnum:"8",
								},
	
    	}
  	}
}
</script>

<style>
	
html{
	font-size: 100%;
}
body{
	font-size: 1em;
	height: 100%;
}
div.main{
	height: 100%;
	background-repeat: no-repeat;
	background-size:cover;
	font-size:16px;
}
.main .head{
	height:4.5em;
}
.main .body{
/*	height: calc(100% - 4.5em);*/
	height: 998px;
}
.main .nopadding{
	padding:0px;
}
.main div.nav{
	height:4.5em;
	background-color: rgba(36,42,47,0.5);
/*	background-image:url(../../images/top.png);
	background-repeat: no-repeat;
	background-size: cover;*/
}
.main div.top_nav{
	height:7em;
}
.main div.nav_list{
	height:100%;
	background-image: url(../assets/Nav/logo.png);
	background-repeat: no-repeat;
	background-position: 1.25em 1em;
}

	
	
	.main #nav_ul{
	float:right;
	list-style: none;
	width:36.75em;
	margin:20px 0px 0px 0px;
	border-radius: 0.93em;
	padding:0px;
	background-image:url(../assets/Nav/top_2.png) ;
	background-repeat: no-repeat;
	margin-right:1.5em;
	color:#FFFFFF;
}
.main #nav_ul>li{
	float:left;
	margin-left:1.25em;
	padding-top:0.31em;
	height:2.125em;
	cursor:pointer;
}
.main #nav_ul span{
	background-size:contain;
	background-repeat:no-repeat ;
	margin-top: 2px;
	margin-right:2px;
	width:18px;
	height:18px;
	cursor: pointer;
}
.main #nav_ul .personal{
	display: block;
    float: left;
	background-image: url(../assets/Nav/user_icon.png);
}
.main #nav_ul .password{
	display: block;
    float: left;
	background-image: url(../assets/Nav/edit.png);
}
.main #nav_ul .quit{
	display: block;
    float: left;
	background-image: url(../assets/Nav/close.png);
}
.main ul.show_list{
	list-style: none;
	margin:0px;
	padding:0px;
}
.main div.right_area{
	margin:0px;
	overflow: hidden;
}
/*//*/
.main ul.top_list {
	float:right;
	margin-top:1.875em;
	margin-right: 10px;
	padding:0px;
}
.main ul.top_list>li{
	list-style: none;
	margin:0px;
	width:8.3em;
	height:4.25em;
	float:left;
	margin-left:0.625em;
}
.main div.nav_icon{
	cursor:pointer;
	color:#fff;
	font-size:1em;
	padding-right:0.625em;
	line-height:4.25em;
	border-radius: 5px;
	background-repeat: no-repeat;
}
.main div.nav_icon>img{
	float:left;
	margin-top:0.5em;
}
.main div.page_main{
	background-color: #dc9d49;
}
.main div.travel{
	background-color: #a4c445;
}
.main div.service{
	background-color: #54d35b;
}
.main div.beyound{
	background-color: #55d0b7;
}
.main div.map{
	background-color: #66b6e2;
}
.main div.user{
	background-color: #bd78b2;
}
.main div.role{
	background-color: #dc9d49;
}
.main div.log{
	background-color: #d07379;
}
.main div.notice{
	background-color: #ef84ab;
}
.main div.doc{
	background-color: #d1a185;
}
.main div.book{
	background-color: #3fbadf;
}
</style>


